// Movie ratings 

.movie-rating {
    .rating-stars {
      display: flex;
      justify-content: center;
      align-items: center;
      
      .star {
        font-size: 24px;
        color: #ccc;
        cursor: pointer;
        
        &:hover,
        &.selected {
          color: #ffcc00;
        }
      }
    }
    
    .rating-label {
      text-align: center;
      margin-top: 10px;
      font-weight: bold;
      
      span {
        color: #ffcc00;
      }
    }
  }
  
  textarea {
    width: 100%;
    height: 100px;
    margin-top: 10px;
  }
  
  button {
    margin-top: 10px;
    padding: 10px 20px;
    background-color: #ffcc00;
    color: #fff;
    border: none;
    cursor: pointer;
    
    &:hover {
      background-color: #ffa500;
    }
  }
  