@use '../base/variables' as v;
@use '../base/mixins' as m;

/**DarkMode**/

.right-way {
    display: flex;
    flex-direction: column;
    align-items: center;

    @include m.tablet {
        align-items: flex-end;
    }

.dark-mode-boton {
    width: 3rem;
    margin-top: 3rem;
    margin-bottom: 2rem;
    filter: invert(100%);

    &:hover {
        cursor: pointer;
    }

        @include m.tablet {
            margin-top: 0;
        }
    }
}

.barra {
    display: flex;
    align-items: center;
    flex-direction: column;
    padding-top: 3rem;

    @include m.tablet {
        flex-direction: row;
        justify-content: space-between;
    }
}
.navegacion {

    visibility: hidden;
    opacity: 0;
    height: 0;
    transition: visibility .3s ease-in-out, opacity .5s linear, height .5s linear; 


    @include m.tablet {
        display: flex;
        align-items: center;
        opacity: 1;
        visibility: visible;
        
    }
    
    a {
        color: v.$blanco;
        text-decoration: none;
        font-size: 2rem;
        display: block;
        text-align: center;
        
        @include m.tablet {
            margin-right: 2rem;
            font-size: 1.8rem;

            &:last-of-type {
                margin-right: 0;
            }
        }
        &:hover {
            color: v.$Naranja;
        }
    }
}

/** Menu de hamburguesa **/
.mobile-menu {
    @include m.tablet {
        display: none;
    }

    img {
        width: 5rem;
    }
}

.mostrar {
    visibility: visible;
    opacity: 1;
    height: auto;
}

